@import './../../global';

.BodyHolder{
    background-color: $background;
    color: $black;
    min-height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.Row{
    $image-size: 30rem;
    $text-size: calc(100% - #{$image-size});
    background-color: $white;
    height: $image-size;
    width: 80%;
    margin-left: 5%;
    margin-right: 5%;
    display: flex;
    flex-direction: row;
    position: relative;
    background-color: $background;
    overflow: hidden;
    &.Reverse{
        flex-direction: row-reverse;
    }
    
    .Image{
        background-color: $background;
        height: $image-size;
        width: $image-size;
        object-fit: cover;
        z-index: 50;
    }
    .Text{
        background-color: $background;
        height: 100%;
        width: 10%;
        width: $text-size;
        position: relative;
        word-break: break-all;
        //center block
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .Block{
            position:absolute;
            top:40%;
            transform: translateY(-50%);
            width: 60%;
            background-color: $background;
            text-align: center;
            .Title{
                color: $white;
            }
            .Body{
    
                color:$white;
            }

        }
        
    }
}